</template>\n\n<script>\nexport default {\n    name: \"CreateVenueItem\",\n    components: {},\n    data() {\n        return {\n            venue: \"\",\n            name: \"\",\n            venues: {\n                items: [],\n                loading: false\n            },\n            loading: false,\n            errors: {}\n        };\n    },\n    watch: {\n        venues: [\n            function search(val) {\n                let self = this;\n                if (self.venues.length > 0 || self.venues.loading) return;\n                self.venues.loading = true;\n                this.$store\n                    .dispatch(\"venue/get\", {})\n                    .then(response => {\n                        self.venues.items = response.data;\n                        self.venues.loading = false;\n                    })\n                    .catch(error => {\n                        self.venues.loading = false;\n                    });\n            }\n        ]\n    },\n    methods: {\n        submit() {\n            let self = this;\n            if (self.loading) {\n                return;\n            }\n            self.loading = true;\n            let data = {\n                venue_id: self.$route.params.id,\n                venue: self.venue,\n                name: self.name\n            };\n            this.$store\n                .dispatch(\"venue/item/create\", data)\n                .then(response => {\n                    self.loading = false;\n                    self.errors = {};\n                    self.$router.back();\n                })\n                .catch(error => {\n                    self.errors = error.response.data?.data;\n                    self.loading = false;\n                });\n        }\n    }\n};\n</script>\n\n<style scoped>\n.v-text-field >>> input {\n    font-size: 0.8em;\n    font-weight: 100;\n}\n.v-text-field >>> label {\n    font-size: 0.8em;\n}\n.v-text-field >>> button {\n    font-size: 0.8em;\n}\n</style>\n"],"sourceRoot